import { Component, OnInit, Input, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ApiClientService } from '../service/api-client.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  public inputForm: FormGroup;
  public isNameValid: boolean;
  public isPasswordValid: boolean;
  constructor(private router: Router, private api: ApiClientService) {
    this.isNameValid = true;
    this.isPasswordValid = true;
  }

  ngOnInit() {
    this.inputForm = new FormGroup({
      userName: new FormControl(null, [
        Validators.required,
        Validators.minLength(2)
      ]),
      passwordText: new FormControl(null, [
        Validators.required,
        Validators.minLength(2)
      ])
    });
  }

  public redirectToRegister(): void {
    this.router.navigate([`register`]);
  }

  public login() {
    if (this.isNameValid && this.isPasswordValid) {
      this.api.login(this.inputForm.get('userName').value, this.inputForm.get('passwordText').value );
    }
  }

  public userNameFocusOut() {
    if (this.inputForm.get('userName').valid) {
      this.isNameValid = true;
    } else {
      this.isNameValid = false;
    }
  }

  public passwordFocusOut() {
    if (this.inputForm.get('passwordText').valid) {
      this.isPasswordValid = true;
    } else {
      this.isPasswordValid = false;
    }
  }
}
