import { Component, OnInit, Output } from '@angular/core';
import { RankingBoardService } from '../service/ranking-board.service';
import { ApiClientService } from '../service/api-client.service';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-score-board',
  templateUrl: './score-board.component.html',
  styleUrls: ['./score-board.component.scss']
})
export class ScoreBoardComponent implements OnInit {
  constructor(private rankingService: RankingBoardService, private apiService: ApiClientService) {
    apiService.contentChanged.subscribe(value => {
      if (value === true) {
        this.ranking = this.rankingService.scores;
        console.log(this.rankingService.scores);
      }
    });
   }

  @Output() ranking;

  ngOnInit() {
  }

}
