import { Component } from '@angular/core';
import { ApiClientService } from './service/api-client.service';
import { AuthServiceService } from './service/auth-service.service';
import { RankingBoardService } from './service/ranking-board.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'PacMan';
  public visible = 'hidden';
  constructor(private apiClient: ApiClientService, private authService: AuthServiceService, private scoreBoard: RankingBoardService) {
    this.apiClient.loggedStateChanged.subscribe(value => {
      if (value === true) {
        if (this.apiClient.loggedIn === true) {
          this.visible = 'visible';
        } else {
          this.visible = 'hidden';
        }
      }
    });
  }

  private logout(): void {
    this.apiClient.logout();
  }
}
