import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GameComponent } from './game/game.component';
import { LoginComponent } from './login/login.component';
import { ScoreBoardComponent } from './score-board/score-board.component';
import { RegisterComponent } from './register/register.component';
import { InfoComponent } from './info/info.component';
import { HomeComponent } from './home/home.component';
import { OnlyLoggedInUsersGuard } from './guard/onlyLoggedUsersGuard';

const routes: Routes = [
    {path: 'game', component: GameComponent, canActivate: [OnlyLoggedInUsersGuard]},
    {path: 'login', component: LoginComponent},
    {path: 'score-board', component: ScoreBoardComponent, canActivate: [OnlyLoggedInUsersGuard]},
    {path: 'register', component: RegisterComponent},
    {path: 'info', component: InfoComponent},
    {path: 'home', component: HomeComponent},
    {path: '**', redirectTo: 'home'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [OnlyLoggedInUsersGuard]
})
export class AppRoutingModule {
}

