import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { AuthServiceService } from './auth-service.service';
import { RankingBoardService } from './ranking-board.service';

@Injectable({
    providedIn: 'root'
})

export class ApiClientService {
    private pollingTimer: any;
    private timer: number;
    private name: string;
    constructor(private http: HttpClient, private auth: AuthServiceService, private scoreBoard: RankingBoardService) {
        this.timer = 5000;
        this.startApiClientPolling();
    }

    public ranking;
    public contentChanged: BehaviorSubject<boolean> = new BehaviorSubject(false);
    public loggedStateChanged: BehaviorSubject<boolean> = new BehaviorSubject(false);
    public loggedIn = false;
    public loginMessage: string;
    public registerMessage: string;

    private polling(): void {
      this.http.get<any>('http://localhost:3000/scoreboard', {responseType: 'json'}).subscribe(data => {
            const jsonTemp = JSON.parse(JSON.stringify(data));
            this.scoreBoard.scores = jsonTemp.ranking;
            this.contentChanged.next (true);
        }, error => console.log(error));
      }

    public login(username: string, password: string): void {
        this.name = username;
        const data = {
          username,
          password
        };

        this.http.post<any>('http://localhost:3000/signin', data).subscribe(
            x => {
                this.auth.token = x.token;
                if (this.auth.token.length > 1) {
                  this.loggedIn = true;
                  this.loginMessage = 'Login successful as ' + this.name;
                  this.loggedStateChanged.next(true);
                 } else {
                  this.loggedIn = false;
                  this.loginMessage = 'Wrong input, are you already registered?';
                }
              },
            error => { console.error(error); this.loginMessage = 'Wrong input, are you already registered?'; });
    }

    public sendScore(score: number, message: string): void {
      const data = {
        score,
        message
      };
      this.http.post<any>('http://localhost:3000/score', data).subscribe(
        x => {},
          error => { console.error(error); });
  }

  public register(username: string, password: string): void {
      const data = {
        username,
        password
      };
      this.http.post<any>('http://localhost:3000/register', data).subscribe(
          x => {
              console.log(x);
              const jsonTemp = JSON.parse(JSON.stringify(x));
              this.registerMessage = jsonTemp.reason;
          },
          error => {
            console.error(error);
            this.registerMessage = JSON.parse(JSON.stringify(error)).error.reason;
          });
      }


      public logout() {
          this.auth.token = '';
          const data = {
            name
          };
          this.http.post<any>('http://localhost:3000/logout', data).subscribe(
            x => {
              this.loggedIn = false;
              this.loginMessage = 'You are logout.';
          },
          error => { console.error(error);
          });
      }

    public startApiClientPolling(): void {
        this.pollingTimer = setInterval(() => {
          this.polling();
      }, this.timer);
    }

    private stop(): void {
        clearInterval(this.pollingTimer);
    }
}
